}

.board {
 border: 2px solid #444;
 border-bottom: none;
 }
.board .row {
 display: -webkit-flex;
 display: flex;
 height: 50px;
 margin: 0px;
 padding: 0px;
 }
.board .row:last-child {
 border-bottom: none;
 height: 50px;
 }
.board .row .cell {
 background-color: grey;
 border: 1.5px solid #444;
 width: 49px;
 display: -webkit-flex;
 display: flex;
 -webkit-justify-content: center;
         justify-content: center;
 -webkit-align-items: center;
         align-items: center;
 
}
.board .row .cell.open {
 background-color: silver;
}
          
.board .row .cell:last-child {
 border-right: none; 
}
 
